body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.full-page {
  width: 100%;
  height: 100vh; /* Full viewport height */
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-section {
  margin-top: 4rem; /* Adjust this value to match the height of the header */
  width: 100%;
  height: calc(100vh - 4rem); /* Subtract the header height from the viewport height */
  display: flex;
  align-items: center;
  justify-content: left;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.home-content {
  display: flex;
  flex-wrap: wrap;
  flex-flow: column;
  text-align: left;
  justify-content: center;
  padding: 2rem;
}

.home-title {
  font-size: 3rem;
  letter-spacing: 0.2rem;
  color: #fff;
  /* White text for contrast */
  margin-bottom: 1rem;
}

.home-subtitle {
  font-size: 1.5rem;
  color: #ddd;
  /* Light gray text for contrast */
  margin-bottom: 2rem;
}

.highlight-text {
  color: #61dafb;
  /* Highlighted text color */
  font-weight: bold;
}

.cta-wrapper {
  margin-top: 1rem;
}

.cta-button {
  display: inline-block;
  padding: 0.75rem 1.5rem;
  background-color: #61dafb;
  color: #fff;
  text-decoration: none;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 0.5rem;
  transition: background-color 0.3s ease;
}

.cta-button:hover {
  background-color: #4ca3d9;
  /* Darker blue on hover */
}

.cta-button.secondary {
  background-color: #282c34;
}

.cta-button.secondary:hover {
  background-color: #444;
}

/* About Section */
.about-section {
  padding: 4rem 2rem;
  background-color: #000000;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  opacity: 0; /* Initially hidden */
  transform: translateY(20px); /* Start position for animation */
}

.about-section.animated {
  opacity: 1; /* Visible when animated */
  transform: translateY(0); /* End position for animation */
}

.about-container {
  display: flex;
  flex-wrap: wrap;
  max-width: 1200px;
  width: 100%;
  gap: 2rem;
}

.about-content {
  flex: 1;
  min-width: 300px;
}

.about-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 1rem;
  color: #fff;
}

.about-subtitle {
  font-size: 1.5rem;
  color: #555;
  margin-bottom: 1rem;
  color: #fff;
}

.about-description {
  font-size: 1rem;
  color: #666;
  margin-bottom: 2rem;
  color: #fff;
}

.about-image {
  flex: 1;
  min-width: 300px;
  text-align: center;
}

.about-image img {
  max-width: 100%;
  border-radius: 0.5rem;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.home-content.animated,
.about-section.animated {
  animation: fadeInUp 1s ease-in-out;
}

/* Services Section */
.services-section {
  padding: 4rem 2rem;
  background-color: #f9f9f9;
}

.services-container {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.services-header {
  margin-bottom: 2rem;
}

.services-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #333;
}

.services-subtitle {
  font-size: 1.2rem;
  color: #666;
}

.services-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
  list-style: none;
  padding: 0;
  margin: 0;
}

services-item {
  flex: 1 1 calc(33.333% - 2rem);
  max-width: calc(33.333% - 2rem);
  background-color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  text-align: left;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.services-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.15);
}

.services-image {
  height: 200px;
  background-size: cover;
  background-position: center;
}

.services-item-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
  margin: 1rem;
}

.services-item-description {
  font-size: 1rem;
  color: #666;
  margin: 0 1rem 1rem;
}

/* Contact Section */
.contact-section {
  padding: 4rem 2rem;
  background-color: #f9f9f9;
}

.contact-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}

.contact-header {
  text-align: center;
  width: 100%;
  margin-bottom: 2rem;
}

.contact-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #333;
}

.contact-subtitle {
  font-size: 1.2rem;
  color: #666;
}

.contact-content {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: space-between;
}

.contact-details {
  flex: 1;
  min-width: 300px;
}

.contact-details h3 {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
}

.contact-details p,
.contact-details ul {
  font-size: 1rem;
  color: #666;
}

.contact-details ul {
  list-style: none;
  padding: 0;
}

.contact-details ul li {
  margin-bottom: 0.5rem;
}

.contact-form {
  flex: 1;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
}

.contact-form button {
  padding: 0.75rem;
  background-color: #282c34;
  color: white;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
}

.contact-form button:hover {
  background-color: #61dafb;
}

/* Responsive styles */
@media (max-width: 768px) {
  .home-title {
    font-size: 2rem;
  }

  .home-subtitle {
    font-size: 1.2rem;
  }

  .cta-button {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
  }

  .services-item {
    flex: 1 1 calc(100% - 2rem);
    max-width: calc(100% - 2rem);
  }

  .contact-content {
    flex-direction: column;
  }
}